import React, { useEffect } from "react";
import { Link } from "react-router-dom";

// Componentes
import ServicesSectionRoad from "../../components/Equipo/ServicesSectionRoad";
import ServicesAccordion from "../../components/Servicios/ServicesAccordion";

// Imagenes
import imagen7 from "../../images/images-services/SERVICIO_2.7.png";

interface listaXD {
  titulo: string;
  texto: any;
}

const ServicioCivil = () => {
  const listaAccordion: listaXD[] = [
    {
      titulo: "Sucesin intestada",
      texto: (
        <p>
          Al ocurrir un deceso sin que exista testamento, los clientes que se
          consideren herederos forzosos del fallecido podrn realizarlo de
          manera notarial o judicial por lo que ponemos a disposicin el
          asesoramiento eficaz para su declaracin.
        </p>
      ),
    },
    {
      titulo: "Redaccin de testamentos",
      texto: (
        <p>
          Realizamos el Estudio de ttulos y elaboracin de testamentos y
          legados para mantener en orden los derechos patrimoniales adquiridos
          en vida por el cliente.
        </p>
      ),
    },
    {
      titulo: "Divorcios",
      texto: (
        <p>
          Efectuamos desde la desvinculacin conyugal, junto con la peticin de
          los derechos conexos al inters superior del nio y derechos
          patrimoniales, ante solicitud de separacin convencional, a nivel
          judicial, municipal y notarial, as como aquellos que posean causal.
        </p>
      ),
    },
    {
      titulo: "Prescripcin adquisitiva y extintiva",
      texto: (
        <p>
          Para adquirir un bien mueble o inmueble por el transcurso del plazo
          legal de la posesin legtima, continua y pacfica; por otro lado, de
          acuerdo con lo prescrito por el Cdigo Civil, por el decurso
          prescriptorio podemos exigir la inexistencia del derecho a accionar
          judicialmente sobre derechos que hayan existido previamente.
        </p>
      ),
    },
    {
      titulo:
        "Indemnizaciones de daos y perjuicios contractual y extracontractual",
      texto: (
        <p>
          Dentro de los plazos legales podemos tramitar las indemnizaciones que
          provengan del cumplimiento tardo o defectuoso de los contratos,
          convenios, relaciones comerciales u otros por alguna de las partes y
          asimismo, pedir la indemnizacin por daos ocasionados por accidentes
          o conductas que perjudiquen al cliente.
        </p>
      ),
    },
    {
      titulo: "Compraventa",
      texto: (
        <p>
          Asesoramos en aquellas negociaciones de compra o venta de bienes
          muebles o inmuebles tanto por el monto como en las condiciones
          especiales que se pretendan.
        </p>
      ),
    },
    {
      titulo: "Inmatriculacin y rectificacin de reas",
      texto: (
        <p>
          Cuando un inmueble no ha sido registrado ante la Oficina Registral de
          Predios podemos efectuar la inmatriculacin del mismo realizando los
          trmites exigibles y en caso de que el rea utilizada del predio no
          sea similar a la registrada realizamos la rectificacin cumpliendo con
          los requisitos para ello.
        </p>
      ),
    },
  ];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <ServicesSectionRoad
        nameOption="Nuestros Servicios"
        numberRoad={3}
        nameRoadOne="Servicios"
        nameRoadTwo="Civil"
      />
      <div className="container my-5">
        <div className="row px-3">
          <div className="col-12 col-md-6">
            <h5 className="text-center fw-bold my-4">CIVIL</h5>
            <p className="text-justify">
              Esta rea est orientada a brindar asesora legal en las distintas
              materias del derecho Civil, con especial ponderacin en el Derecho
              Civil Patrimonial. El rea se encarga de elaborar los distintos
              contratos regulados en nuestro ordenamiento civil vigente tales
              como Compra venta, arrendamiento, locacin de servicios, fianza,
              entre otros, diseando asimismo, estructuras contractuales
              atpicas que se adecuen a los requerimientos especficos de
              nuestros clientes.
            </p>
            <p className="text-justify">
              Esta rea tambin proporciona asesora en Derecho de Familia
              (divorcios de mutuo acuerdo notariales o municipales, o aquellos
              con causal), Sucesiones, Derechos reales, representacin derechos
              reales de garanta y dems reas.
            </p>
            <Link
              to="/Contacto"
              style={{ background: "#c17743", color: "#fff" }}
              className="btn my-5 w-50 d-block mx-auto"
            >
              Contctanos
            </Link>
          </div>
          <div className="col-12 col-md-6">
            <div className="mx-4">
              <img
                className="w-100 shadow rounded"
                src={imagen7}
                alt="Imagen de servicio"
              />
            </div>
          </div>
        </div>
        <div className="row my-5 px-3">
          <div className="col-12 col-md-2"></div>
          <div className="col-12 col-md-8">
            <p className="fw-bold text-center">
              Algunos ejemplos de nuestra asesora:
            </p>
            <ServicesAccordion id="accordionUno" lista={listaAccordion} />
            <p className="text-justify my-5">
              Cuando un inmueble no ha sido registrado ante la Oficina Registral
              de Predios podemos efectuar la inmatriculacin del mismo
              realizando los trmites exigibles y en caso de que el rea
              utilizada del predio no sea similar a la registrada realizamos la
              rectificacin cumpliendo con los requisitos para ello.
            </p>
          </div>
          <div className="col-12 col-md-2"></div>
        </div>
      </div>
    </>
  );
};

export default ServicioCivil;
