import React, { useEffect } from "react";
import { Link } from "react-router-dom";

// Componentes
import ServicesSectionRoad from "../../components/Equipo/ServicesSectionRoad";

// Imagenes
import imagen8 from "../../images/images-services/SERVICIO_2.8.png";

const ServicioPenal = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <ServicesSectionRoad
        nameOption="Nuestros Servicios"
        numberRoad={3}
        nameRoadOne="Servicios"
        nameRoadTwo="Penal"
      />
      <div className="container my-5">
        <div className="row px-3">
          <div className="col-12 col-md-6">
            <h5 className="text-center fw-bold my-4">PENAL</h5>
            <p className="text-justify">
              El Estudio posee amplia experiencia en la asesora legal en la
              defensa de los bienes jurdicos vulnerados por la comisin de
              ilcitos penales de nuestros clientes, as como, en la defensa de
              una supuesta conducta indebida tipificada por nuestro ordenamiento
              penal cometida por nuestros clientes.
            </p>
            <p className="text-justify">
              Nuestra asesora comprende la prevencin, la dilucidacin, la
              prosecucin y la provisin de actos que se cometan en contra de
              nuestros clientes, como tambin el improbable caso contrario.
            </p>
            <Link
              to="/Contacto"
              style={{ background: "#c17743", color: "#fff" }}
              className="btn my-5 w-50 d-block mx-auto"
            >
              Contctanos
            </Link>
          </div>
          <div className="col-12 col-md-6">
            <div className="mx-4">
              <img
                className="w-100 shadow rounded"
                src={imagen8}
                alt="Imagen de servicio"
              />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ServicioPenal;
