import React, { useEffect } from "react";
import { Link } from "react-router-dom";

// Componentes
import ServicesSectionRoad from "../../components/Equipo/ServicesSectionRoad";
import ServicesAccordion from "../../components/Servicios/ServicesAccordion";

// Imagenes
import imagen1 from "../../images/images-services/SERVICIO_2.1.jpg";

interface listaXD {
  titulo: string;
  texto: any;
}

const ServicioCorporativa = () => {
  const listaAccordion: listaXD[] = [
    {
      titulo: "Contitucin de sociedades peruanas",
      texto: (
        <p>
          Sociedad annimas abiertas, sociedades annimas por oferta pblica,
          sociedades annimas.
          <br /> Sociedad comercial de responsabilidad limitada.
          <br />
          Sociedades colectivas.
          <br /> Sociedades civiles: Ordinarias y de responsabilidad limitada.
          <br /> Sociedades en comandita: simple y por acciones.
        </p>
      ),
    },
    {
      titulo: "Creacin de sucursales",
      texto: <p>En el Per y en el extranjero.</p>,
    },
    {
      titulo: "Contratos participativos",
      texto: <p>En asociacin, consorcio.</p>,
    },
    {
      titulo: "Fusiones",
      texto: (
        <p>
          Unir dos o ms empresas con un solo capital y accionariado ya sea por
          integracin o por absorcin.
        </p>
      ),
    },
    {
      titulo: "Escisiones",
      texto: <p>Dividir una sociedad en dos o ms grupos societarios.</p>,
    },
    {
      titulo: "Aumento de capital",
      texto: (
        <p>
          Por aportes nuevos, por aporte de bienes y por distribucin de
          utilidades.
        </p>
      ),
    },
    {
      titulo:
        "Elaboracin de actas de junta general de accionistas y seguimiento de acuerdos",
      texto: "",
    },
    {
      titulo: "Impugnacin de acuerdos",
      texto: (
        <p>
          Los socios pueden disentir de los acuerdos tomados en la Junta de
          Accionistas, asesoramos la oposicin de estos de acuerdo con las
          normas legales vigentes.
        </p>
      ),
    },
  ];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <ServicesSectionRoad
        nameOption="Nuestros Servicios"
        numberRoad={3}
        nameRoadOne="Servicios"
        nameRoadTwo="Corporativa"
      />
      <div className="container my-5">
        <div className="row px-3">
          <div className="col-12 col-md-6">
            <h5 className="text-center fw-bold my-4">
              CORPORATIVA Y MERCANTIL
            </h5>
            <p className="text-justify">
              Nuestro Estudio ha desarrollado una vasta experiencia en la
              creacin de estructuras corporativas buscando que las mismas se
              conviertan en una herramienta adecuada para el desarrollo de los
              negocios de nuestros clientes.
            </p>
            <p className="text-justify">
              Ello supone adems, una completa asesora para la formacin de
              sociedades civiles y mercantiles, as como, en relacin a actos
              mercantiles trascendentes como la reorganizacin de empresas,
              fusiones y escisiones, disolucin y liquidacin de personas
              jurdicas y otros.
            </p>
            <Link
              to="/Contacto"
              style={{ background: "#c17743", color: "#fff" }}
              className="btn my-5 w-50 d-block mx-auto"
            >
              Contctanos
            </Link>
          </div>
          <div className="col-12 col-md-6">
            <div className="mx-4">
              <img
                className="w-100 shadow rounded"
                src={imagen1}
                alt="Imagen de servicio"
              />
            </div>
          </div>
        </div>
        <div className="row my-5 px-3">
          <div className="col-12 col-md-2"></div>
          <div className="col-12 col-md-8">
            <p className="fw-bold text-center">
              Algunos ejemplos de nuestra asesora:
            </p>
            <ServicesAccordion id="accordionUno" lista={listaAccordion} />
          </div>
          <div className="col-12 col-md-2"></div>
        </div>
      </div>
    </>
  );
};

export default ServicioCorporativa;
